* {
  font-size: 62.5%;
}
.characters {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.character {
  display: flex;
  flex-direction: column;
  background: linear-gradient(
    0deg,
    rgba(106, 57, 119, 1) 30%,
    rgba(174, 226, 117, 1) 100%
  );
  width: 25rem;
  height: 35rem;
  margin: 0rem 3rem 12rem 3rem;
  border: 0.3rem solid #fff;
  border-radius: 25px;
  padding: 1rem;
  color: #fff;
  box-shadow: 0px 0px 40px 5px rgba(106, 57, 119, 0.75);
}

.image {
  width: 15rem;
  height: 15rem;
  border-radius: 25px;
  margin: 0 auto;
}
.characteristic {
  text-align: center;
}
.name {
  font-size: 3rem;
}
.status,
.species {
  font-size: 2rem;
}

@media screen and (min-width: 1024px) {
  .character {
    width: 35rem;
    height: 45rem;
    margin: 0rem 6rem 12rem 6rem;
  }

  .image {
    width: 20rem;
    height: 20rem;
  }
  .name {
    font-size: 4rem;
  }
  .status,
  .species {
    font-size: 2.5rem;
  }
}
